@import "../../styles/variables";

.tablet {
  width: max-content;
  font-size: 0.9375rem;
  font-weight: map-get($font-weight, "bold");
  padding: 0.5em 0.6em;
  color: $primaryDarkCyan;
  background-color: $cyanBackground;
  border-radius: 3px;

  display: flex;
  gap: 1rem;

  &:hover {
    background-color: $primaryDarkCyan;
    color: white;
    cursor: pointer;
  }
}
