@import "../styles/variables";

.filterContainer {
  background-color: white;
  padding: 1rem;
  display: flex;
  box-shadow: 2px 5px 10px 1px rgba(black, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tablets {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.clear {
  color: $primaryDarkCyan;
  font-weight: map-get($font-weight, "bold");

  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}
