@import "../styles/variables";

.flag {
  display: inline-block;
  text-transform: uppercase;
  font-size: 0.7rem;
  color: white;

  padding: 0.6em 0.6em;
  padding-bottom: 0.3em;
  margin-right: 0.5rem;

  border-radius: 15px;
}

.card {
  margin: 5vh auto;
  padding: 1.2rem;
  background-color: white;
  border-radius: 5px;

  box-shadow: 2px 8px 10px 1px rgba(black, 0.2);
}
.cardFeatured {
  @extend .card;
  border-left: 4px solid $primaryDarkCyan;
}

.new {
  @extend .flag;
  background-color: $primaryDarkCyan;
}
.featured {
  @extend .flag;
  background-color: $veryDarkGrayishCyan;
}

.jobDetail {
  text-align: left;
  border-bottom: $darkGrayishCyan 1px solid;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

.name {
  color: $primaryDarkCyan;
  font-size: 0.9rem;
  font-weight: map-get($font-weight, "bold");
  margin-right: 0.8rem;
}
.jobTitle,
.jobInfo {
  color: $veryDarkGrayishCyan;
}

.jobTitle {
  font-weight: map-get($font-weight, "bold");
  margin: 0.75rem 0;
  &:hover {
    cursor: pointer;
    color: $primaryDarkCyan;
  }
}
.jobInfo {
  font-size: 0.8rem;
  color: $darkGrayishCyan;
  .info + .info {
    margin-left: 1rem;
  }
}
.tablets {
  display: flex;
  flex-wrap: wrap;
  gap: 0.7rem;
}

@media screen and (min-width: 900px) {
  .card {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .jobDetail {
    border: none;
  }
}
